@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  /* background-color: #3f3f3f; */
}


.cert-date {
  display: flex;
  color: white;
  text-shadow: 0.05em 0.05em black;
 }

.cert-date__month {
  font-size: 1rem;
  font-weight: normal;
  margin-right: .5vw;
}

.cert-date__year {
  font-size: 1rem;
  font-weight: normal;
  margin-right: .5vw;
}

.cert-date__day {
  font-size: 1rem;
  font-weight: n;
  margin-right: .5vw;
}

.cert-item-wrapper {
  width: min(700px, 100vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
}

.cert-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.1rem;
  padding-inline: 1.5rem;
  margin-bottom: 1vh;
  margin-inline: 2vw;
  background-color: #7f9eb2;
  border: 1px solid black;
  border-radius: 5px;
  
}

.cert-item h2 {
  width: 40%;
  font-weight: normal;
  font-size: .8rem;
  font-style: italic;
  -webkit-margin-before: 0.3em;
          margin-block-start: 0.3em;
  -webkit-margin-after: 0.3em;
          margin-block-end: 0.3em;
  border: 1px solid black;
  padding: 0.1rem;
  padding-inline: 1vw;
  background-color: white;
  border-radius: 5px;
}

.Cert-item__subjectName {
  width: 80%;
  background-color: transparent;
  color: black;
  padding-top: 1vh;
  margin-left: 2rem;
  margin-bottom: 1vh;
  font-size: 1.2rem;
}

.certs {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
}

.infoText1 {
  background-color: white;
  width: min(700px, 100vw);
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 1vh auto 0.3vh;
  border: 2px solid rgb(0, 0, 0);
  border-radius: 10px;
  color: rgb(13, 13, 13);
}

.infoText1 img {
  margin-left: 2vw;

  width: 70%;
  object-fit: cover;
}

.infoText1 h2 {
  width: 60%;
  font-family: "daytona";
  margin-inline: auto;
  font-size: 2rem;
  font-style: italic;
  font-weight: bold;
  -webkit-margin-before: 0.3em;
          margin-block-start: 0.3em;
  -webkit-margin-after: 0.3em;
          margin-block-end: 0.3em;
}

.infoText2 {
  background-color: white;
  width: min(700px, 100vw);
  display: flex;
  margin: 0.3vh auto 0.3vh;
  color: rgb(13, 13, 13);
  justify-content: flex-end;
}

.infoText2 p {
  font-size: 0.75rem;
  -webkit-margin-before: 0.2em;
          margin-block-start: 0.2em;
  -webkit-margin-after: 0.2em;
          margin-block-end: 0.2em;
}

.certFormToggle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(500px, 100vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 1vh;
  margin-bottom: 3vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
}

button {
  border: none;
  background-color: transparent;
  line-height: 100%;
  height: 100%;
  width: 100%;
  font-size: 1.1rem;
}

.formLayout {
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(500px, 100vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 1vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
  margin-bottom: 3vh;
}

.create-form,
.field {
  width: 100%;
}

.label {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-inline: 10%;
  margin-top: .4vh;
  margin-bottom: .8vh;
  
}

input {

background-color: #7f9eb2;
color: white;
padding-left: 1vh;
}

input::-webkit-input-placeholder {
color: rgb(212, 206, 199);
padding-left: 1vh;
}

input::placeholder {
color: rgb(212, 206, 199);
padding-left: 1vh;
}

.submitButtonWrapper {

    display: flex;
    justify-content: center;
}

.submitButton {

background-color: black;
color: white;
width: 30%;
border-radius: 5px;
margin-top: 1.5vh;
padding: .5vh;
}

.submitButton:hover{

    cursor: pointer;
}
.AppFrame {

    padding-bottom: 2vh;
}
