.certs {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
}

.infoText1 {
  background-color: white;
  width: min(700px, 100vw);
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 1vh auto 0.3vh;
  border: 2px solid rgb(0, 0, 0);
  border-radius: 10px;
  color: rgb(13, 13, 13);
}

.infoText1 img {
  margin-left: 2vw;

  width: 70%;
  object-fit: cover;
}

.infoText1 h2 {
  width: 60%;
  font-family: "daytona";
  margin-inline: auto;
  font-size: 2rem;
  font-style: italic;
  font-weight: bold;
  margin-block-start: 0.3em;
  margin-block-end: 0.3em;
}

.infoText2 {
  background-color: white;
  width: min(700px, 100vw);
  display: flex;
  margin: 0.3vh auto 0.3vh;
  color: rgb(13, 13, 13);
  justify-content: flex-end;
}

.infoText2 p {
  font-size: 0.75rem;
  margin-block-start: 0.2em;
  margin-block-end: 0.2em;
}
