.certFormToggle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(500px, 100vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 1vh;
  margin-bottom: 3vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
}

button {
  border: none;
  background-color: transparent;
  line-height: 100%;
  height: 100%;
  width: 100%;
  font-size: 1.1rem;
}

.formLayout {
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(500px, 100vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 1vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
  margin-bottom: 3vh;
}

.create-form,
.field {
  width: 100%;
}

.label {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-inline: 10%;
  margin-top: .4vh;
  margin-bottom: .8vh;
  
}

input {

background-color: #7f9eb2;
color: white;
padding-left: 1vh;
}

input::placeholder {
color: rgb(212, 206, 199);
padding-left: 1vh;
}

.submitButtonWrapper {

    display: flex;
    justify-content: center;
}

.submitButton {

background-color: black;
color: white;
width: 30%;
border-radius: 5px;
margin-top: 1.5vh;
padding: .5vh;
}

.submitButton:hover{

    cursor: pointer;
}